<template>
  <BasePopUp
    :show="!!fileList.length"
    :title="$t('upload.title')"
    @close="cancelUpload()">
    <div
      class="popup-upload-area">
      <BaseUploadBar
        v-for="(file, index) of fileList"
        :key="index"
        :progress="uploadPercentage[index]"
        :filename="file.name"
        :status="getStatus(file.name)"
        class="upload-bar"/>
    </div>
    <div class="popup-text">
      <BaseDropDown
        :label="$t('upload.choose_license')"
        :options="[
          {
            label: 'CC-BY',
            value: 'cc-by',
          },
          {
            label: 'CC0',
            value: 'cc0',
          },
          {
            label: $t('nolicense'),
            value: 'no license',
          }
        ]"
        :show-label="true"
        :header-background-color="'rgb(240, 240, 240)'"
        v-model="license"
        class="upload-dropdown"/>
      <BaseDropDown
        :options="[
          { label: $t('no'), value: 'false' },
          { label: $t('yes'), value: 'true' }]"
        :header-background-color="'rgb(240, 240, 240)'"
        :label="$t('upload.publish_images')"
        :show-label="true"
        v-model="publish"
        class="upload-dropdown"/>
    </div>
    <template
      slot="button-row">
      <BaseButton
        v-if="isInitial"
        :text="$t('cancel')"
        :icon="'remove'"
        :icon-position="'right'"
        :icon-size="'small'"
        class="base-upload-bar-button"
        @clicked="cancelUpload"
      />
      <!-- @event buttonRight -->
      <BaseButton
        :text="isInitial || isSaving ? $t('upload.upload') : $t('upload.done')"
        :icon="!isSaving ? 'check-mark' : ''"
        :icon-position="'right'"
        :icon-size="'small'"
        :disabled="isSaving"
        class="base-upload-bar-button"
        @clicked="startUpload">
        <template
          v-if="isSaving"
          slot="right-of-text">
          <span class="base-upload-bar-loader">
            <BaseLoader />
          </span>
        </template>
      </BaseButton>
    </template>
  </BasePopUp>
</template>

<script>
import {
  BasePopUp,
  BaseDropDown,
  BaseInput,
  BaseUploadBar,
  BaseButton,
  BaseLoader,
} from 'base-ui-components';
import axios from 'axios';
// import upload from '../assets/file-upload.fake.service';

const STATUS_INITIAL = 0;
const STATUS_SAVING = 1;
const STATUS_SUCCESS = 2;
const STATUS_FAILED = 3;

export default {
  components: {
    BaseButton,
    BasePopUp,
    BaseDropDown,
    BaseInput,
    BaseUploadBar,
    BaseLoader,
  },
  props: {
    fileList: {
      type: Array,
      default() {
        return [];
      },
    },
  },
  data() {
    return {
      uploadedFiles: [],
      rejectedFiles: [],
      uploadError: null,
      currentStatus: null,
      uploadPercentage: [],
      publish: { label: this.$t('no'), value: 'false' },
      license: {
        label: this.$t('nolicense'),
        value: 'no license',
      },
    };
  },
  computed: {
    isInitial() {
      return this.currentStatus === STATUS_INITIAL;
    },
    isSaving() {
      return this.currentStatus === STATUS_SAVING;
    },
    isSuccess() {
      return this.currentStatus === STATUS_SUCCESS;
    },
    isFailed() {
      return this.currentStatus === STATUS_FAILED;
    },
  },
  mounted() {
    this.reset();
  },
  methods: {
    async startUpload() {
      if (this.uploadedFiles.length) {
        this.$emit('success');
      } else {
        if (!this.fileList.length) return;
        await Promise.all(this.fileList
          .map((file, index) => new Promise(async (resolve, reject) => {
            const formData = new FormData();

            formData.append('file', file);
            formData.append('entry', this.$route.params.id);
            // These values can not be empty (also no empty string) or it will give
            // an error
            formData.append('published', this.publish.value);
            formData.append('license', this.license.value);

            this.currentStatus = STATUS_SAVING;
            this.$emit('upload-start');

            try {
              await axios.post(`${process.env.API}media/`,
                formData,
                {
                  withCredentials: true,
                  xsrfCookieName: 'csrftoken_portfolio',
                  xsrfHeaderName: 'X-CSRFToken',
                  headers: {
                    'Content-Type': 'multipart/form-data; boundary=----WebKitFormBoundary7MA4YWxkTrZu0gW',
                  },
                  onUploadProgress: (progressEvent) => {
                    this.uploadPercentage[index] = Math
                      .round(progressEvent.loaded / progressEvent.total);
                  },
                });
              this.uploadedFiles.push(file.name);
              this.currentStatus = STATUS_SUCCESS;
              resolve();
            } catch (err) {
              this.uploadPercentage[index] = 0;
              this.uploadError = err.response;
              this.currentStatus = STATUS_FAILED;
              this.rejectedFiles.push(file.name);
              reject();
            }
          })));
        // TODO: duplicated in Attachments --> see if this can be made more efficient
        // maybe place in attachment area component?
        try {
          await this.$store.dispatch('data/fetchMediaData', this.$route.params.id);
        } catch (e) {
          console.error(e);
          this.$notify({
            group: 'request-notifications',
            title: this.$t('notify.somethingWrong'),
            text: this.$t('notify.fetchMediaFail'),
            type: 'error',
          });
        }
      }
    },
    cancelUpload() {
      this.$emit('cancel');
      this.reset();
    },
    reset() {
      this.currentStatus = STATUS_INITIAL;
      this.uploadedFiles = [];
      this.rejectedFiles = [];
      this.publish = { label: this.$t('no'), value: false };
      this.license = {
        label: this.$t('nolicense'),
        value: 'no license',
      };
      this.uploadError = null;
      this.fileList.forEach((file, index) => this.$set(this.uploadPercentage, index, 0));
    },
    getStatus(fileName) {
      if (this.uploadedFiles.includes(fileName)) {
        return 'success';
      }
      if (this.rejectedFiles.includes(fileName)) {
        return 'fail';
      }
      return '';
    },
  },
};
</script>

<style lang="scss" scoped>
  @import "../styles/variables.scss";

  .popup-text {
    display: flex;
    align-items: flex-end;

    .upload-dropdown {
      width: 100%;
    }
  }

  .popup-text > div:first-of-type {
    margin-right: $spacing;
  }

  .files-popup-input-field {
    margin-bottom: $spacing;
  }

  .popup-upload-area {

    .upload-bar {
      margin-bottom: $spacing;
    }
  }

  .base-upload-bar-button {
    flex-basis: 100%;

    .base-upload-bar-loader {
      position: relative;
      transform: scale(0.5);
      margin-left: $spacing;
      padding-left: $spacing;
    }
  }

  .base-upload-bar-button + .base-upload-bar-button {
    margin-left: $spacing;
  }

  @media screen and (max-width: $mobile) {
    .base-upload-bar-button + .base-upload-bar-button {
      margin-left: 0;
      margin-top: $spacing;
    }
  }
</style>
